.profile {
  display: grid;
  grid-template: 1fr / 1fr minmax(auto, 1400px) 1fr;
}

.profile__block {
  grid-column: 2/3;
  padding: 10px;
  margin: 0;
  display: grid;
  grid-template: 1fr 1fr / 1fr minmax(auto,500px) minmax(auto,900px) 1fr;
}

.profile__block span {
  font-weight: bold;
  padding-bottom: 10px;
}

.block__data {
  grid-column: 2/3;
  grid-row: 1/2;

  background-color: rgb(245, 245, 245);
  text-align: center;
}

.block__form {
  grid-column: 3/4;
  grid-row: 1/2;

  padding-left: 10px;
  background-color: rgb(245, 245, 245);
  margin-left: 20px;
}

.block__wall {
  grid-column: 3/4;
  grid-row: 2/3;

  padding-left: 10px;
  background-color: rgb(245, 245, 245);
  margin-left: 20px;
  margin-top: 20px;
}

.unitInfo a{
color: black;
}

.unitInfo a:hover{
  color:rgb(99, 76, 41);
  opacity: 1.1;
}

.contactblock {
  padding-left: 10px;
  color: rgb(77, 77, 77);
}

.buttonEditActiveted {
  padding-top: 20px;
}

.buttonEditDiactiveted {
  padding-top: 20px;
}

.fullNameUser {
  font-weight: 900;
  font-size: 37px;
}

.profileStatus {
  padding: 20px 0px 20px 0px;
}

.infoProfile span{
  color: rgb(102, 87, 58);
  opacity: 0.8;
}

.contactInformation{
  color: rgb(83, 68, 36);
}

.changePhoto{
  margin-top: 10px;
}

.information{
  font-size: 22px;
  font-weight: 700;
  margin-top: 10px;
  color: rgb(83, 68, 36);
}

.blocMessageOnWall{
  margin-top: 10px;
  margin-bottom: 20px;
}

.photoMainProfile{
  margin-bottom: 20px;
}

.general__subscribe,
.general__button {
  margin: 0px 3px;
  color: white;
  background-color: black;
}
.general__subscribe:hover,
.general__button:hover {
  transform: scale(1.1);
  color: rgb(147, 109, 76);
}