.message {
  display: grid;
  grid-template: 1fr / 1fr minmax(auto, 1400px) 1fr;
}

.message__block {
  grid-column: 2/3;
  text-align: center;
  display: grid;
  grid-template: 1fr / 1fr minmax(auto, 300px) minmax(auto, 800px) 1fr;
}

.block__title {
  grid-column: 2/3;
  display: grid;
  text-align: center;
  padding: 18px;
  margin: 20px 0px;
  font-size: 25px;
  font-weight: 800;
}

.block__title_color {
  text-decoration: underline;
}

.block__title_border {
  border-top: 1px solid rgb(150, 150, 150, 0.2);
  border-bottom: 1px solid rgb(150, 150, 150, 0.2);
}

.block__users {
  grid-column: 2/3;

  text-align: center;
  border-right: 3px solid white;
}

.block__dialogs {
  grid-column: 3/4;
  text-align: left;
  margin-left: 10px;
  padding-left: 10px;
}

.users__user {
  height: 600px;
  overflow-y: scroll;
}

.user__block {
  position: relative;
  margin: 5px;
  padding: 5px;
  height: 60px;
}

.user__block img {
  border-radius: 50px;
  float: left;
}

.user__block b {
  position: absolute;
  top: 50%;
  left: 40%;
  transform: translate(0, -50%);
}

.dialogs__item {
  display: grid;
  grid-template: 1fr / 1fr minmax(auto, 200px) minmax(auto, 400px) minmax(
      auto,
      200px
    ) minmax(auto, 100px) 1fr;
  margin-top: 20px;
}

.body__name {
  grid-column: 2/3;
}

.body__message {
  grid-column: 3/4;
}

.item__time {
  grid-column: 4/5;
  text-align: right;
  padding-right: 10px;
}

.body__message_delete {
  grid-column: 5/6;
  opacity: 0.1;
}

.body__message_delete img {
  width: 20px;
  height: 20px;
}

.body__message_delete:hover {
  opacity: 1.1;
}

.users__title {
  color: rgb(156, 129, 89);
  font-size: 28px;
  padding-left: 15px;
}

.dialogs__button {
  padding-top: 20px;
  padding-bottom: 20px;
  text-align: center;
}

.dialogs__sendBlock{
  display: grid;
  grid-template: 1fr / minmax(auto,640px) minmax(auto,50px);
  text-align: left;
}

.dialogs__text{
  grid-column: 1/2;
}


.dialogs__button_color {
  color: white;
  background-color: black;
  padding: 5px;
  grid-column: 2/3;
}

.dialogs__button_color:hover {
  transform: scale(1.1);
  color: rgb(156, 129, 89);
}